<template>
    <div class="header">
        <div class="logo">
            <img :src="require('../../assets/img/logo.png')"/>
        </div>
        <div class="name">{{sitename}}</div>
        <div class="menu" @click.stop="open()">
            <img :src="require('../../assets/img/menu.png')"/>
        </div>
        <div class="clear"></div>
    </div>
</template>
<script>
    import { mapState } from 'vuex'
    export default {
        props: {
            open: {
                type: Function
            }
        },
        computed: {
            ...mapState(["sitename"]),
        },
        data(){
            return {

            }
        },
        methods: {

        }
    }
</script>
<style>
    .header{
        height: 50px;
        background: white;
        border-bottom: 1px solid #eeeeee;
        box-sizing: border-box;
    }
    .header img{
        border-radius: 50%;
        width: 100%;
    }
    .header .logo{
        position: absolute;
        left: 20px;
        top: 10px;
        height: 30px;
        width: 30px;
    }
    .header .name{
        position: absolute;
        left: 80px;
        top: 15px;
        font-size: 20px;
        height: 30px;
        line-height: 30px;
        display: inline-block;
    }
    .header .menu{
        position: absolute;
        right: 20px;
        top: 10px;
        height: 30px;
        width: 30px;
    }
    .clear{
        clear: both;
    }
    @media (min-width: 300px) {
        .header {
            height: 60px;
        }
        .header .logo {
            height: 40px;
            width: 40px;
        }
        .header .menu {
            height: 40px;
            width: 40px;
        }
    }
    @media (min-width: 400px) {
        .header {
            height: 65px;
        }
        .header .logo {
            height: 45px;
            width: 45px;
        }
        .header .menu {
            height: 45px;
            width: 45px;
        }
        .header .name {
            left: 88px;
            top: 18px;
        }
    }
    @media (min-width: 500px) {
        .header {
            height: 70px;
        }
        .header .logo {
            height: 50px;
            width: 50px;
        }
        .header .menu {
            height: 50px;
            width: 50px;
        }
        .header .name {
            left: 90px;
            top: 20px;
        }
    }
    @media (min-width: 768px) {
        .header {
            height: 90px;
        }
        .header .logo {
            top: 15px;
            height: 60px;
            width: 60px;
            left: 50px;
        }
        .header .menu {
            top: 15px;
            height: 60px;
            width: 60px;
            right: 40px
        }
        .header .name {
            left: 150px;
            top: 32px;
        }
    }
    @media (min-width: 992px) {
        .header {
            height: 100px;
        }
        .header .logo {
            top: 15px;
            height: 70px;
            width: 70px;
            left: 60px;
        }
        .header .name {
            left: 160px;
        }
        .header .menu {
            top: 15px;
            height: 70px;
            width: 70px;
            right: 50px;
        }
    }
</style>


