<template>
    <div id="home">
        <div class="page-menu">
            <DrawerContent :status="status"></DrawerContent>
        </div>
        <div class="page-main" :class="status ? 'c-state' : ''" @click.stop="close()">
            <div class="mask" v-if="status"></div>
            <div class="ispc">
                <div class="mapWraper">
                    <div id="header">
                        <PcHeader :option="option"></PcHeader>
                    </div>
                    <div id="container">
                        <div class="map">
                            <div id="map"></div>
                        </div>

                        <div class="contactWrapper">
                            <div class="contact">
                                <div class="company">联系我们</div>
                                <div class="item">
                                    <div class="title">地址</div>
                                    <div class="detail">浙江省温州市瓯海区振革路38号</div>
                                </div>
                                <div class="item">
                                    <div class="title">电话</div>
                                    <div class="detail">0577-88888888</div>
                                </div>
                                <div class="item">
                                    <div class="title">QQ</div>
                                    <div class="detail">2574522520</div>
                                </div>
                                <div class="item">
                                    <div class="title">邮箱</div>
                                    <div class="detail">9527@qq.com</div>
                                </div>
                                <p class="sbtime">我们的工作从周一到周五(08:30am - 17:30pm) <br>欢迎咨询我们</p>
                            </div>
                        </div>
                    </div>
                    <div id="footer">
                        <PcFooter></PcFooter>
                    </div>
                </div>
            </div>
            <div class="ismobile">
                <MobileHeader class="mobile_header" :open="open"></MobileHeader>
                <div class="mobileContent">
                    <div class="m-contact">
                        <div id="mapMobile"></div>
                    </div>
                    <MobileFooter class="mobile_footer"></MobileFooter>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import AMap from 'AMap'
    import PcHeader from '../Home/PcHeader.vue'
    import PcFooter from '../Home/PcFooter.vue'
    import MobileHeader from '../Home/MobileHeader.vue'
    import MobileFooter from '../Home/MobileFooter.vue'
    import DrawerContent from '../Home/DrawerContent.vue'

    export default {
        data(){
            return {
                location: [120.213197, 30.179933],
                option: 'contact',
                status: false,
            }
        },
        methods: {
            initMap(){
                var map = new AMap.Map('map',{
                    resizeEnable: true,
                    zoom: 15,
                    center: this.location,
                    scrollWheel:false,
                    mapStyle: 'amap://styles/dark'
                });

                AMap.plugin('AMap.ToolBar',function(){
                    var toolbar = new AMap.ToolBar();
                    map.addControl(toolbar)
                });

                var marker = new AMap.Marker({
                    position: this.location,
                    title: '瓯柑科技',
                    map: map
                });
            },
            initMobileMap(){
                var map = new AMap.Map('mapMobile',{
                    resizeEnable: true,
                    zoom: 15,
                    center: this.location,
                    scrollWheel:false,
                    mapStyle: 'amap://styles/dark'
                });

                var marker = new AMap.Marker({
                    position: this.location,
                    title: '瓯柑科技',
                    map: map
                });
            },
            open(){
                if (this.status) {
                    this.status = false;
                } else {
                    this.status = true;
                }
            },
            close(){
                if (this.status) {
                    this.status = false;
                }
            },
        },
        components: {
            PcHeader,
            PcFooter,
            MobileHeader,
            MobileFooter,
            DrawerContent
        },
        mounted(){
            if(/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
                this.initMobileMap();
            }
            else{
                this.initMap();
            }
        }
    }
</script>
<style scoped="scoped">
    @import '../../assets/css/base.css';
    .mapWraper{
        height:100%;
        width:100%;
        overflow-y: auto;
    }
    #header{
        height: 90px;
        overflow: hidden;
    }
    #container{
        width:100%;
        height: 680px;
        position: relative;
    }
    #container .map{
        width:100%;
        height:100%;
    }
    .contactWrapper{
        width: 90%;
        margin: 0 auto;
    }
    .contact{
        width: 380px;
        height: 420px;
        background-color: #FFF;
        position: absolute;
        top: 90px;
        padding: 34px 27px 0px 43px;
        overflow: hidden;
    }
    .contact .company{
        font-size: 30px;
        color: #c11920;
        line-height: 50px;
    }
    .contact .item{
        font-size: 16px;
        margin-top:  12px;
    }
    .contact .item .title{
        color: #c4c4c4;
        font-size: 16px;
    }
    .contact .item .detail{
        color: #636363;
        font-size: 16px;
        margin-top:  5px;
    }
    .contact .sbtime{
        margin: 0px;
        font-size: 14px;
        line-height: 18px;
        border-top: 1px dotted #636363;
        margin-top: 20px;
        padding-top: 18px;
    }
    #map, #province{
        height:100%;
        width:100%;
    }
    .clear{
        clear: both;
    }
    #footer{
        min-height: 200px;
        border-top: solid 4px #c11920;
    }
    .m-contact{
        height: calc( 100% - 60px );
    }
    #mapMobile{
        height:100%;
        width:100%;
    }

    @media (min-width: 300px){
        .m-contact{
            height: calc( 100% - 60px );
        }
    }
    @media (min-width: 400px){
        .m-contact{
            height: calc( 100% - 65px );
        }
    }
    @media (min-width: 500px){
        .m-contact{
            height: calc( 100% - 70px );
        }
    }
    @media (min-width: 768px){
        .m-contact{
            height: calc( 100% - 90px );
        }
    }
    @media (min-width: 992px){
        #header{
            height: 90px;
        }
        .contactWrapper{
            width: 900px;
        }
    }
    @media (min-width: 1200px){
        #header{
            height: 100px;
        }
        .contactWrapper{
            width: 1000px;
        }
    }
    @media (min-width: 1500px){
        #header{
            height: 110px;
        }
        .contactWrapper{
            width: 1200px;
        }
    }
    @media (min-width: 1800px){
        #header{
            height: 120px;
        }
        .contactWrapper{
            width: 1500px;
        }
    }
</style>


