            }
        },
        data(){
            return {
                pshow: false,
                img: require('../../assets/img/series1.png')
            }
        },
        computed: {
            ...mapState(["sitename"]),
        },
        methods: {
            enter(){
                this.pshow = true;
            },
            leave(){
                this.pshow = false;
            },
            imgenter(){
                var idx = Math.round(Math.random());
                if(idx == 0){
                    this.img = require('../../assets/img/series1.png');
                }
                else{
                    this.img = require('../../assets/img/series2.png');
                }
            }
        }
