<template>
    <div id="home">
        <div class="page-menu">
            <DrawerContent :status="status"></DrawerContent>
        </div>
        <div class="page-main ishome" :class="status ? 'c-state' : ''" @click.stop="close()">
            <div class="mask" v-if="status"></div>
            <MobileHeader class="mobile_header" :open="open"></MobileHeader>
            <PcHeader class="pc_header" :option="option"></PcHeader>
            <div class="content pcContent">
                <div class="banner">
                    <Banner :list="list"></Banner>
                </div>
                <div class="bg_grey">
                    <div class="service">
                        <Service></Service>
                    </div>
                </div>
                <div class="bg_white">
                    <div class="case">
                        <Case></Case>
                    </div>
                </div>
                <div class="bg_grey">
                    <div class="news">
                        <News></News>
                    </div>
                </div>

                <About class="bg_white"></About>

                <PcFooter class="pc_footer" :ishome="'yes'"></PcFooter>
                <MobileFooter class="mobile_footer"></MobileFooter>
            </div>
        </div>
    </div>
</template>
<script>
    import Vue from 'vue'
    import Banner from './Banner.vue'
    import Case from './Case.vue'
    import News from './News.vue'
    import Service from './Service.vue'
    import PcHeader from '../Home/PcHeader.vue'
    import PcFooter from '../Home/PcFooter.vue'
    import MobileHeader from '../Home/MobileHeader.vue'
    import MobileFooter from '../Home/MobileFooter.vue'
    import About from '../Home/About.vue'
    import DrawerContent from '../Home/DrawerContent.vue'

    export default {
        data(){
            return {
                list: [
                    {src: require('../../assets/img/banner1.jpg'), desc: '', name: 'item 1'},
                    {src: require('../../assets/img/banner2.jpg'), desc: '', name: 'item 2'},
                    {src: require('../../assets/img/banner3.jpg'), desc: '', name: 'item 3'},
                    {src: require('../../assets/img/banner4.jpg'), desc: '', name: 'item 4'}
                ],
                status: false,
                pshow: false,
                option: 'home'
            }
        },
        methods: {
            open(){
                if (this.status) {
                    this.status = false;
                } else {
                    this.status = true;
                }
            },
            close(){
                if (this.status) {
                    this.status = false;
                }
            },
            enter(){
                this.pshow = true;
            },
            leave(){
                this.pshow = false;
            }
        },
        components: {
            Banner,
            Case,
            News,
            Service,
            PcFooter,
            PcHeader,
            MobileHeader,
            MobileFooter,
            About,
            DrawerContent
        }
    }

</script>
<style>
    ul{
        list-style: none;
        padding: 0px;
        margin: 0px;
    }
    a {
        color: #363f44;
        text-decoration: none;
        cursor: pointer;
    }
    a:hover,
    a:focus {
        color: #141719;
        text-decoration: none;
    }
    .pcContent{
        overflow: auto;
    }

    @import '../../assets/css/home.css';
    @media (min-width: 300px) and (max-width: 400px){
        .pcContent{
            height: calc( 100% - 60px );
        }
    }
    @media (min-width: 400px) and (max-width: 500px){
        .pcContent{
            height: calc( 100% - 65px );
        }
    }
    @media (min-width: 500px) and (max-width: 768px){
        .pcContent{
            height: calc( 100% - 70px );
        }
    }
    @media (min-width: 768px) and (max-width: 992px){
        .pcContent{
            height: calc( 100% - 90px );
        }
    }
</style>


