<template>
    <div class="news-content" :class="active ? 'active' : ''">
        <div class="n-title">
            <div class="p1">新闻中心</div>
            <div class="p2">秉承专注创新的民族精神</div>
        </div>
        <ul class="news-ul">
            <router-link to="/news">
                <router-link to="/news">
                    <li class="n-item" v-for="item in 4" :key="item">
                        <div class="pic-content">
                            <div class="pic">
                                <img :src="require('../../assets/img/banner3.jpg')"/>
                            </div>
                            <div class="title">射雕英雄传</div>
                        </div>
                        <div class="sub-title">“曼”步星空公益计划启动，助力10万自闭症儿童打开心锁</div>
                        <div class="desc">我们会联合星缘守护，成立志愿者队伍，接下来会陆续开展自闭症儿童第二课堂、亲近海洋生物等活动，用孩子们比较容易接受的形式，帮助他们打开心锁</div>
                        <div class="more">read more</div>
                    </li>
                </router-link>
            </router-link>
            <div class="clear"></div>
        </ul>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                active: false
            }
        },
        methods: {
            scroll(){
                let obj = document.querySelector(".ishome");
                let swidth = document.body.clientWidth;
                let screenHeight = obj.offsetHeight;   //屏幕可见高度
                let scrollTop = obj.scrollTop;   //滚动条距离顶部高度

                let contentObj = document.querySelector(".content");
                let theObj = document.querySelector(".news");
                let theObjH= theObj.offsetHeight;
                let theObjT= theObj.offsetTop + contentObj.offsetTop;
                if(swidth >= 768){
                    /**
                     * objT < (scrollTop + screenHeight) 元素顶部可见   从上往下滚动
                     * (objT + objH) > scrollTop元素底部可见   从下往上滚动
                     */
                    if((theObjT + theObjH) >= scrollTop && theObjT < (scrollTop + screenHeight)){
                        this.active = true;
                    }
                    else{
                        this.active = false;
                    }
                }
            }
        },
        mounted(){
            let contentObj = document.querySelector(".ishome");
            contentObj.addEventListener('scroll', this.scroll);

            this.scroll();
        }
    }
</script>
<style>
    .news-ul{
        list-style: none;
        padding: 0px;
        margin: 0px;
        width: 100%;
    }
    .news .n-item{
        background: white;
        min-height: 200px;
        margin-bottom: 15px;
    }
    .news-ul li{
        float: left;
        translate-webkit-transition: all .5s;
        -moz-transition: all .5s;
        transition: all .5s;
    }
    .news .n-item:last-child{
        margin-bottom: 0px;
    }
    .pic-content{
        position: relative;
        min-height: 120px;
    }
    .pic-content .pic img{
        width: 100%;
    }
    .pic-content .title{
        position: absolute;
        bottom: 0px;
        background: white;
        width: 80%;
        left: 10%;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #80734a;
        font-size: 16px;
    }
    .n-item .sub-title{
        text-align: center;
        color: #4c4c4c;
        line-height: 32px;
        font-size: 16px;
        padding-left: 10%;
        padding-right: 10%;
        margin-top: 5%;
        height: 64px;
    }
    .n-item .desc{
        padding-left: 5%;
        padding-right: 5%;
        margin-top: 5%;
        color: #4c4c4c;
        line-height: 24px;
        text-align: center;
        height: 72px;
        overflow: hidden;
        font-size: 12px;
    }
    .n-item .more{
        padding-bottom: 5%;
        text-align: center;
        color: #80734a;
        font-weight: bold;
        font-size: 16px;
        margin-top: 5%;
    }
    .news-content.active .n-title, .news-content.active .news-ul{
        opacity: 0;
    }
    .news-content.active .n-title, .news-content.active .news-ul{
        animation: fadeIn 1s ease  forwards  alternate;
        -webkit-animation: fadeIn 1s ease  forwards  alternate;
    }
    .news-content.active .news-ul{
        -webkit-animation-delay: 0.3s;
        animation-delay: 0.3s;
    }
    @media (min-width: 300px){
        .news-ul li{
            width: 100%;
        }
    }
    @media (min-width: 400px) {
        .news-ul li {
            width: 100%;
        }
    }
    @media (min-width: 500px) {
        .news-ul li {
            width: 100%;
        }
    }
    @media (min-width: 768px) {
        .news-ul li {
            width: 49%;
        }
    }
    @media (min-width: 992px) {
        .news-ul li {
            width: 49%;
        }
        .news-ul li:nth-child(odd) {
            margin-right: 2%;
        }
    }
    @media (min-width: 1200px){
        .news-ul li{
            cursor: pointer;
            width: 23%;
            margin-left: 1%;
            margin-right: 1%;
        }
        .news-ul li:hover{
            -webkit-box-shadow:1px 2px 8px rgba(0,0,0,0.3);
            -moz-box-shadow:1px 2px 8px rgba(0,0,0,0.3);
            box-shadow:1px 2px 8px rgba(0,0,0,0.3);
        }
        .news-ul li:nth-child(odd){
            margin-right: 1%;
        }
        .news-ul li:hover{
            box-shadow: 0 10px 16px rgba(0,0,0,.1);
            transform: translateY(-15px);  -webkit-transform:translateY(-15px);
        }
        .news-ul{
            width: 1100px;
            margin: 0px auto;
        }
    }
    @media (min-width: 1500px){
        .news-ul{
            width: 1300px;
            margin: 0px auto;
        }
    }
    @media (min-width: 1800px){
        .news-ul{
            width: 1500px;
            margin: 0px auto;
        }
    }
</style>


