<template>
    <div id="home">
        <div class="page-menu">
            <DrawerContent :status="status"></DrawerContent>
        </div>
        <div class="page-main" :class="status ? 'c-state' : ''" @click.stop="close()">
            <div class="mask" v-if="status"></div>
            <div class="ispc" v-if="!isMobile">
                <div class="contentWraper">
                    <div id="header">
                        <PcHeader :option="option"></PcHeader>
                    </div>
                    <div id="container" class="bgnews" style="background: #fff;">
                        <div class="main_banner"></div>
                        <div class="news-list">
                            <ul class="listWrapper">
                                <router-link to="/detail">
                                    <li class="item" v-for="(v, k) in list" :key="k">
                                        <div class="left">
                                            <div class="day">29</div>
                                            <div class="date">2018-06</div>
                                        </div>
                                        <div class="middle">
                                            <img :src="require('../../assets/img/banner3.jpg')"/>
                                        </div>
                                        <div class="right">
                                            <div class="title">恪守匠心•赢在执行|亚合大魅力年会，hold住光辉岁月</div>
                                            <div class="desc">2018年1月20日，浙江亚合大机电科技有限公司在云天楼•假日皇都大酒店举行了以“恪守匠心•赢在执行”为主题的迎春表彰晚会。现场宾朋满座，大家在洋溢着欢欣喜气的氛围中辞旧迎新，享受缘分带给我们的欢乐，感受亚合大这个大家庭的温馨。</div>
                                        </div>
                                    </li>
                                </router-link>
                            </ul>

                            <div class="pageWrapper">
                                <el-pagination
                                        @size-change="handleSizeChange"
                                        @current-change="handleCurrentChange"
                                        :current-page.sync="currentPage"
                                        :page-size="perPage"
                                        layout="total, prev, pager, next"
                                        :total="total">
                                </el-pagination>
                            </div>
                        </div>
                    </div>
                    <div id="footer">
                        <PcFooter></PcFooter>
                    </div>
                </div>
            </div>
            <div class="ismobile" v-if="isMobile">
                <MobileHeader class="mobile_header" :open="open"></MobileHeader>
                <div class="mobileContent">
                    <div class="m-news">
                        <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
                            <router-link to="/detail">
                                <li  v-for="(v, k) in list" :key="k">
                                    <div class="time">
                                        <span class="day">29</span>
                                        <span class="date">2018-06</span>
                                    </div>
                                    <div class="img">
                                        <img :src="require('../../assets/img/banner3.jpg')"/>
                                    </div>
                                    <div class="title">恪守匠心•赢在执行|亚合大魅力年会，hold住光辉岁月</div>
                                    <div class="desc">2018年1月20日，浙江亚合大机电科技有限公司在云天楼•假日皇都大酒店举行了以“恪守匠心•赢在执行”为主题的迎春表彰晚会。现场宾朋满座，大家在洋溢着欢欣喜气的氛围中辞旧迎新，享受缘分带给我们的欢乐，感受亚合大这个大家庭的温馨。</div>
                                </li>
                            </router-link>
                        </ul>
                        <div class="loadingWrapper">
                            <mt-spinner type="fading-circle" color="#26a2ff" v-if="loading"></mt-spinner>
                        </div>
                    </div>
                    <MobileFooter class="mobile_footer"></MobileFooter>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import PcHeader from '../Home/PcHeader.vue'
    import PcFooter from '../Home/PcFooter.vue'
    import MobileHeader from '../Home/MobileHeader.vue'
    import MobileFooter from '../Home/MobileFooter.vue'
    import DrawerContent from '../Home/DrawerContent.vue'
    import Vue from 'vue'
    import {
        Pagination,
        Loading
    } from 'element-ui';
    import { InfiniteScroll, Spinner } from 'mint-ui'

    Vue.use(Loading);
    Vue.use(Pagination);
    Vue.use(InfiniteScroll);
    Vue.component(Spinner.name, Spinner);

    export default {
        data(){
            return {
                list: [1,2,3,4,5],
                currentPage: 1,
                perPage: 10,
                total: 100,
                option: 'news',
                status: false,
                loading: false,
                screenWidth: document.body.clientWidth,
                isMobile: false
            }
        },
        methods: {
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.users();
            },
            open(){
                if (this.status) {
                    this.status = false;
                } else {
                    this.status = true;
                }
            },
            close(){
                if (this.status) {
                    this.status = false;
                }
            },
            loadMore(){
                if(!this.loading) {
                    this.loading = true;
                    setTimeout(() => {
                        let last = this.list[this.list.length - 1];
                        for (let i = 1; i <= 5; i++) {
                            this.list.push(last + i);
                        }
                        this.loading = false;
                    }, 2000);
                }
            }
        },
        components: {
            PcHeader,
            PcFooter,
            MobileHeader,
            MobileFooter,
            DrawerContent
        },
        watch: {
            screenWidth (newVal, oldVal) {
                this.screenWidth = newVal;
                if(screenWidth >= 992){
                    this.isMobile = false;
                }
                else{
                    this.isMobile = true;
                }
            }
        },
        mounted(){
            const that = this;
            if(document.body.clientWidth >= 992){
                that.isMobile = false;
            }
            else{
                that.isMobile = true;
            }

            window.onresize = () => {
                return (() => {
                    window.screenWidth = document.body.clientWidth;
                    that.screenWidth = window.screenWidth;
                })();
            }
        }
    }
</script>
<style scoped="scoped">
    @import '../../assets/css/base.css';
    #container.bgnews{
        background: #fff;
    }
    .main_banner{
        height: 400px;
        background: transparent url(../../assets/img/news.jpg) no-repeat scroll center center;
    }
    .news-list {
        margin: 0 auto;
        padding-bottom: 30px;
        width: 90%;
        margin-top: 50px;
    }
    .listWrapper{
        list-style: none;
        margin:0;
        padding:0;
        margin-bottom: 50px;
    }
    .listWrapper .item{
        padding-top: 15px;
        padding-bottom: 15px;
        display: flex;
        flex-direction: row;
        min-height: 160px;
        transition: all .3s;
        border-bottom: 1px solid #e6e6e6;
    }
    .listWrapper .item:hover{
        transform: translateX(-20px);
        background: #f5f5f5;
        color:#c6a860;
        cursor: pointer;
    }
    .listWrapper .item .left{
        width: 10%;
        box-sizing: border-box;
        height: 100%;
        padding-left: 15px;
    }
    .listWrapper .item .middle{
        width: 30%;
        box-sizing: border-box;
        height: 100%;
        padding-left: 20px;
    }
    .listWrapper .item .right{
        width: 60%;
        box-sizing: border-box;
        height: 100%;
        padding-left: 30px;
        padding-right: 15px;
    }
    .listWrapper .day{
        font-size: 48px;
        height: 60px;
        line-height: 60px;
        color: #ababab;
        border-bottom: 1px solid #e3e3e3;
        text-align: center;
        padding-bottom: 15px;
        padding-top: 10px;
        -webkit-transition: all .3s;
        -moz-transition: all .3s;
        transition: all .3s;
    }
    .listWrapper .date{
        color: #b2b2b2;
        margin-top: 25px;
        text-align: center;
        -webkit-transition: all .3s;
        -moz-transition: all .3s;
        transition: all .3s;
    }
    .listWrapper .item:hover .day, .listWrapper .item:hover .date, .listWrapper .item:hover .title{
        color:#c6a860;
    }
    .listWrapper .middle img{
        width:100%;
    }
    .listWrapper .title{
        color: #333;
        font-size: 18px;
        -webkit-transition: all .3s;
        -moz-transition: all .3s;
        transition: all .3s;
    }
    .listWrapper .desc{
        color: #666;
        line-height: 24px;
        margin-top: 10px;
        border-top: 1px solid #e6e6e6;
        padding-top: 15px;
        padding-bottom: 15px;
    }
    .pageWrapper{
        width: 90%;
        margin: 0 auto;
        padding-bottom: 50px;
    }
    .pageWrapper .el-pagination button, .pageWrapper .el-pagination span{
        font-size: 14px;
        min-width: 30px;
        height: 30px;
        line-height: 30px;
    }
    .pageWrapper .el-pager li{
        font-size: 14px;
        min-width: 30px;
        height: 30px;
        line-height: 30px;
    }
    .listWrapper a{
        text-decoration: none;
    }
    .m-news{
        min-height: 700px;
    }
    .m-news a{
        text-decoration: none;
    }
    .m-news ul{
        list-style: none;
        margin: 0;
        width: 100%;
        box-sizing: border-box;
        padding: 5px 20px;
        background: #fff;
    }
    .m-news li{
        width: 100%;
        background: #fff;
        border-bottom: 1px solid #eeeeee;
        position: relative;
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .m-news li:after{
        content:"";
        position: absolute;
        bottom:0;
        left: 0;
        width: 100%;
        border-bottom: 1px solid #eeeeee;
    }
    .m-news li:last-child:after{
        border-bottom: none;
    }
    .m-news li .day{
        font-size: 18px;
        height: 22px;
        line-height: 22px;
        color: #ababab;
    }
    .m-news li .date{
        font-size: 16px;
        margin-left: 10px;
        color: #b2b2b2;
    }
    .m-news li .img{
        width: 100%;
        margin-top: 5%;
    }
    .m-news li .img img{
        width: 100%;
    }
    .m-news li .title{
        font-size: 16px;
        color: #333;
        margin-top: 15px;
    }
    .m-news li .desc{
        margin-top: 5%;
        color: #333333;
        font-size: 12px;
        line-height: 20px;
        overflow : hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        max-height: 60px;
        -webkit-box-orient: vertical;
    }
    .loadingWrapper{
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    @media (min-width: 992px){
        .pageWrapper{
            width: 900px;
        }
        .news-list{
            width: 900px;
            padding-bottom: 30px;
        }
        .listWrapper .item{
            min-height: 150px;
            padding-top: 15px;
            padding-bottom: 15px;
        }
    }
    @media (min-width: 1200px){
        .pageWrapper{
            width: 1000px;
            padding-bottom: 50px;
        }
        .news-list{
            width: 1000px;
            padding-bottom: 35px;
        }
        .pageWrapper .el-pagination button, .pageWrapper .el-pagination span{
            font-size: 16px;
            min-width: 40px;
            height: 40px;
            line-height: 40px;
        }
        .pageWrapper .el-pager li{
            font-size: 16px;
            min-width: 40px;
            height: 40px;
            line-height: 40px;
        }
        .listWrapper .item{
            min-height: 170px;
            padding-top: 18px;
            padding-bottom: 18px;
        }
    }
    @media (min-width: 1500px){
        .pageWrapper{
            width: 1200px;
            padding-bottom: 60px;
        }
        .news-list{
            width: 1200px;
            padding-bottom: 40px;
        }
        .pageWrapper .el-pagination button, .pageWrapper .el-pagination span{
            font-size: 18px;
            min-width: 45px;
            height: 45px;
            line-height: 45px;
        }
        .pageWrapper .el-pager li{
            font-size: 18px;
            min-width: 45px;
            height: 45px;
            line-height: 45px;
        }
        .listWrapper .item{
            min-height: 190px;
            padding-top: 20px;
            padding-bottom: 20px;
        }
    }
    @media (min-width: 1800px){
        .pageWrapper{
            width: 1500px;
            padding-bottom: 70px;
        }
        .news-list{
            width: 1500px;
            padding-bottom: 50px;
        }
        .pageWrapper .el-pagination button, .pageWrapper .el-pagination span{
            font-size: 20px;
            min-width: 50px;
            height: 50px;
            line-height: 50px;
        }
        .pageWrapper .el-pager li{
            font-size: 20px;
            min-width: 50px;
            height: 50px;
            line-height: 50px;
        }
        .listWrapper .item{
            min-height: 200px;
            padding-top: 25px;
            padding-bottom: 25px;
        }
    }
</style>


