<script>
    import PcHeader from '../Home/PcHeader.vue'
    import PcFooter from '../Home/PcFooter.vue'
    import MobileHeader from '../Home/MobileHeader.vue'
    import MobileFooter from '../Home/MobileFooter.vue'
    import DrawerContent from '../Home/DrawerContent.vue'
    import Vue from 'vue'
    import {
        Pagination,
        Loading
    } from 'element-ui'
    import { InfiniteScroll, Spinner } from 'mint-ui'

    Vue.use(Loading);
    Vue.use(Pagination);
    Vue.use(InfiniteScroll);
    Vue.component(Spinner.name, Spinner);

    export default {
        data(){
            return {
                list: [1,2,3,4,5],
                currentPage: 1,
                perPage: 10,
                total: 100,
                screenWidth: document.body.clientWidth,
                option: 'case',
                status: false,
                loading: false,
                isMobile: false
            }
        },
        methods: {
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.users();
            },
            open(){
                if (this.status) {
                    this.status = false;
                } else {
                    this.status = true;
                }
            },
            close(){
                if (this.status) {
                    this.status = false;
                }
            },
            loadMore(){
                if(!this.loading) {
                    this.loading = true;
                    setTimeout(() => {
                        let last = this.list[this.list.length - 1];
                        for (let i = 1; i <= 5; i++) {
                            this.list.push(last + i);
                        }
                        this.loading = false;
                    }, 2000);
                }
            }
        },
        components: {
            PcHeader,
            PcFooter,
            MobileHeader,
            MobileFooter,
            DrawerContent
        },
        watch: {
            screenWidth (newVal, oldVal) {
                this.screenWidth = newVal;
                if(screenWidth >= 992){
                    this.isMobile = false;
                }
                else{
                    this.isMobile = true;
                }
            }
        },
        mounted(){
            const that = this;
            if(document.body.clientWidth >= 992){
                that.isMobile = false;
            }
            else{
                that.isMobile = true;
            }

            window.onresize = () => {
                return (() => {
                    window.screenWidth = document.body.clientWidth;
                    that.screenWidth = window.screenWidth;
                })();
            }
        }
    }
</script>
<style>
    @import '../../assets/css/base.css';
    .page-desc{
        width: 900px;
        margin: 0 auto;
        height: 160px;
        overflow: hidden;
        text-align: center;
    }
    .page-desc .title{
        color: #666;
        font-size: 30px;
        margin-top: 35px;
        line-height: 50px;
    }
    .page-desc .desc{
        color: #ccc;
        font-size: 14px;
        line-height: 30px;
    }
    .hp-used .used-wrap {
        margin: 0 auto;
        padding-bottom: 30px;
        padding-right: 6px;
        padding-left: 6px;
        flex-wrap: wrap;
        justify-content: space-between;
        overflow: hidden;
        width: 90%;
    }
    .pageWrapper{
        width: 90%;
        margin: 0 auto;
        padding-bottom: 50px;
    }
    .flex {
        display: -moz-box;
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
    }
    .hp-used .used-wrap .used-box {
        float: left;
        width: 32%;
        background: #fff;
        margin-bottom: 14px;
        transition: all .4s ease 0s;
    }
    a {
        background: transparent;
        text-decoration: none;
    }
    .hp-used .used-wrap .used-box .used-box-pic {
        position: relative;
        transition: all .4s ease 0s;
        height: 160px;
        overflow: hidden;
    }
    .hp-used .used-wrap .used-box .used-box-pic img {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        transition: all .4s;
    }
    .hp-used .used-wrap .used-box .used-box-pic .img1 {
        transition: all .4s ease 0s;
        opacity: 1;
    }
    .hp-used .used-wrap .used-box .used-box-text {
        padding: 5px 15px 0px 15px;
        box-sizing: border-box;
    }
    .hp-used .used-wrap .used-box .used-box-text .title {
        text-align: center;
        padding-top: 12px;
    }
    .hp-used .used-wrap .used-box .used-box-text .title h2 {
        font-size: 18px;
        color: #333;
        margin-bottom: 4px;
    }
    .hp-used .used-wrap .used-box .used-box-text p {
        font-size: 12px;
        color: #999;
        height: 45px;
        overflow: hidden;
        text-align: center;
    }
    .hp-used .used-wrap .used-box:hover{box-shadow:0px 0px 9px #ccc}
    .hp-used .used-wrap .used-box:hover .used-box-pic .img1{opacity:0}
    .hp-used .used-wrap .used-box:hover .used-box-pic img{transform:scale(1.15) translateZ(0)}
    .pageWrapper .el-pagination button, .pageWrapper .el-pagination span{
        font-size: 14px;
        min-width: 30px;
        height: 30px;
        line-height: 30px;
    }
    .pageWrapper .el-pager li{
        font-size: 14px;
        min-width: 30px;
        height: 30px;
        line-height: 30px;
    }
    .m-case{
        min-height: 700px;
    }
    .m-case ul{
        list-style: none;
        margin: 0;
        width: 100%;
        box-sizing: border-box;
        padding: 5px 20px;
        background: #fff;
    }
    .m-case li{
        width: 100%;
        background: #fff;
        position: relative;
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .m-case li:after{
        content:"";
        position: absolute;
        bottom:0;
        left: 0;
        width: 100%;
        border-bottom: 1px solid #eeeeee;
    }
    .m-case li:last-child:after{
        border-bottom: none;
    }
    .m-case li .img{
        width: 100%;
    }
    .m-case li .img img{
        width: 100%;
    }
    .m-case li .title{
        font-size: 16px;
        color: #333;
        margin-top: 15px;
        text-align: center;
    }
    .m-case li .desc{
        text-align: center;
        margin-top: 4%;
        color: #333333;
        font-size: 14px;
        line-height: 25px;
        overflow : hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        max-height: 60px;
        -webkit-box-orient: vertical;
    }
    .loadingWrapper{
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    @media (min-width: 992px){
