            width: 1200px;
            padding-bottom: 60px;
        }
        .hp-used .used-wrap{
            width: 1200px;
            padding-bottom: 40px;
        }
        .hp-used .used-wrap .used-box .used-box-pic {
            height: 240px;
        }
        .hp-used .used-wrap .used-box .used-box-text .title{
            padding-top: 18px;
        }
        .hp-used .used-wrap .used-box{
            margin-bottom: 20px;
        }
        .pageWrapper .el-pagination button, .pageWrapper .el-pagination span{
            font-size: 18px;
            min-width: 45px;
            height: 45px;
            line-height: 45px;
        }
        .pageWrapper .el-pager li{
            font-size: 18px;
            min-width: 45px;
            height: 45px;
            line-height: 45px;
        }
    }
    @media (min-width: 1800px){
        .page-desc{
            width: 1500px;
            height: 220px;
        }
        .page-desc .title{
            margin-top: 60px;
            line-height: 70px;
        }
        .pageWrapper{
            width: 1500px;
            padding-bottom: 70px;
        }
        .hp-used .used-wrap{
            width: 1500px;
            padding-bottom: 50px;
        }
        .hp-used .used-wrap .used-box .used-box-pic {
            height: 280px;
        }
        .hp-used .used-wrap .used-box .used-box-text .title{
            padding-top: 24px;
        }
        .hp-used .used-wrap .used-box{
            margin-bottom: 24px;
        }
        .pageWrapper .el-pagination button, .pageWrapper .el-pagination span{
            font-size: 20px;
            min-width: 50px;
