<template>
    <div id="home">
        <div class="page-menu">
            <DrawerContent :status="status"></DrawerContent>
        </div>
        <div class="page-main" :class="status ? 'c-state' : ''" @click.stop="close()">
            <div class="mask" v-if="status"></div>
            <div class="ispc">
                <div class="contentWraper">
                    <PcHeader id="header" :option="option"></PcHeader>
                    <div id="container" class="bgnews" style="background: #f4f4f4;">
                        <div class="news-detail">
                            <div class="titleWrapper">
                                <div class="title">恪守匠心•赢在执行|亚合大魅力年会，hold住光辉岁月</div>
                                <div class="time">发布时间：2018-06-28</div>
                            </div>
                            <div class="detailWrapper">
                                <div>2018年1月20日，浙江亚合大机电科技有限公司在云天楼•假日皇都大酒店举行了以“恪守匠心•赢在执行”为主题的迎春表彰晚会。现场宾朋满座，大家在洋溢着欢欣喜气的氛围中辞旧迎新，享受缘分带给我们的欢乐，感受亚合大这个大家庭的温馨。</div>
                            </div>
                        </div>
                    </div>
                    <PcFooter id="footer"></PcFooter>
                </div>
            </div>
            <div class="ismobile">
                <MobileHeader class="mobile_header" :open="open"></MobileHeader>
                <div class="mobileContent">
                    <div class="m-detail">
                        <div class="titleWrapper">
                            <div class="title">恪守匠心•赢在执行|亚合大魅力年会，hold住光辉岁月</div>
                            <div class="time">发布时间：2018-06-28</div>
                        </div>
                        <div class="detailWrapper">
                            <div>2018年1月20日，浙江亚合大机电科技有限公司在云天楼•假日皇都大酒店举行了以“恪守匠心•赢在执行”为主题的迎春表彰晚会。现场宾朋满座，大家在洋溢着欢欣喜气的氛围中辞旧迎新，享受缘分带给我们的欢乐，感受亚合大这个大家庭的温馨。</div>
                        </div>
                    </div>
                    <MobileFooter class="mobile_footer"></MobileFooter>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import PcHeader from '../Home/PcHeader.vue'
    import PcFooter from '../Home/PcFooter.vue'
    import MobileHeader from '../Home/MobileHeader.vue'
    import MobileFooter from '../Home/MobileFooter.vue'
    import DrawerContent from '../Home/DrawerContent.vue'
    import Vue from 'vue'
    import {
        Loading
    } from 'element-ui'

    Vue.use(Loading);

    export default {
        data(){
            return {
                option: 'news',
                status: false,
            }
        },
        methods: {
            open(){
                if (this.status) {
                    this.status = false;
                } else {
                    this.status = true;
                }
            },
            close(){
                if (this.status) {
                    this.status = false;
                }
            },
        },
        components: {
            PcHeader,
            PcFooter,
            MobileHeader,
            MobileFooter,
            DrawerContent
        },
        mounted(){

        }
    }
</script>
<style>
    @import '../../assets/css/base.css';
    #container.bgnews{
        background: #f4f4f4;
        border-top: 1px solid #e6e6e6;
        border-bottom: 1px solid #e6e6e6;
    }
    .news-detail {
        margin: 0 auto;
        width: 90%;
    }
    .titleWrapper{
        padding-top: 50px;
        padding-bottom: 30px;
        border-bottom: 1px solid #ddd;
    }
    .detailWrapper{
        margin: 30px 15px;
        min-height: 500px;
    }
    .titleWrapper .title{
        text-align: center;
        font-size: 22px;
        color: #333;
        line-height: 35px;
    }
    .titleWrapper .time{
        text-align: center;
        margin-top: 1%;
        text-align: center;
        color: #888;
        font-size: 16px;
    }
    .m-detail{
        min-height: 700px;
    }

    @media (min-width: 300px){
        .titleWrapper {
            padding-top: 20px;
            padding-bottom: 15px;
        }
        .titleWrapper .title{
            font-size: 18px;
            line-height: 25px;
            text-align: left;
            padding: 5px 15px;
        }
        .titleWrapper .time{
            text-align: right;
            padding-right: 15px;
            font-size: 13px;
        }
        .detailWrapper{
            margin: 20px 15px;
        }
    }
    @media (min-width: 992px){
        .news-detail{
            width: 900px;
            padding-bottom: 30px;
        }
        .titleWrapper {
            padding-top: 50px;
            padding-bottom: 30px;
        }
        .titleWrapper .title{
            font-size: 22px;
            line-height: 35px;
            text-align: center;
            padding: 0px;
        }
        .titleWrapper .time{
            text-align: center;
            padding-right: 0px;
        }
        .detailWrapper{
            margin: 30px 15px;
        }
    }
    @media (min-width: 1200px){
        .news-detail{
            width: 1000px;
            padding-bottom: 35px;
        }
    }
    @media (min-width: 1500px){
        .news-detail{
            width: 1200px;
            padding-bottom: 40px;
        }
    }
    @media (min-width: 1800px){
        .news-detail{
            width: 1500px;
            padding-bottom: 50px;
        }
    }
</style>


