<template>
    <div class="about" :class="active ? 'active' : ''">
        <div class="content">
            <div class="n-title">
                <div class="p1">关于我们</div>
                <div class="p2">亦余心之所善兮，虽九死其犹未悔</div>
            </div>
            <div class="detail">
                <ul>
                    <li>瑞典VOC指纹锁品牌授权合作制造商，旗下授权制造或合作生产VOC体系中的：智能锁（VOC smart lock）、智能门（VOC Door）、磁锁（VOC Magnetic lock）、指纹柜（VOC smart Safes）、守护系统（VOC | GS），5大系列产品。</li>
                    <li>亚合大继续沿用和参与VOC特色的极具创新的协同开发体系，其体系利用互联网协同设计、开发、改进VOC产品。至今已有15个国家和地区中的1700余人有偿无偿地参与VOC产品的开发与改进。仅本企业每年有近70项专利产生。</li>
                    <li>作为新兴工业设计倡导者，VOC产品造型简洁优雅，注重形式美感，不依赖装饰；倡导功能简单易用，追求苛刻的用户体验感。致力于为用户提供高端的易用的智能产品。</li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                active: false
            }
        },
        methods: {
            scroll(){
                let obj = document.querySelector(".ishome");
                let swidth = document.body.clientWidth;
                let screenHeight = obj.offsetHeight;   //屏幕可见高度
                let scrollTop = obj.scrollTop;   //滚动条距离顶部高度

                let contentObj = document.querySelector(".content");
                let aboutObj = document.querySelector(".about");
                let aboutObjH= aboutObj.offsetHeight;
                let aboutObjT= aboutObj.offsetTop + contentObj.offsetTop;

                if(swidth >= 678){
                    /**
                     * aboutObjT < (scrollTop + screenHeight) 元素顶部可见   从上往下滚动
                     * (aboutObjT + aboutObjH) > scrollTop元素底部可见   从下往上滚动
                     */
                    if((aboutObjT + aboutObjH) >= scrollTop && aboutObjT < (scrollTop + screenHeight)){
                        this.active = true;
                    }
                    else{
                        this.active = false;
                    }
                }
            }
        },
        mounted() {
            var that = this;
            var contentObj = document.querySelector(".ishome");
            contentObj.addEventListener('scroll', that.scroll);
        }
    }
</script>
<style>
    .about{
        box-sizing: border-box;
        width: 100%;
        background: #fff;
        color: #333;
    }
    .about .content{
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .about .detail{
        min-height: 100px;
    }
    .about .n-title, .about .n-title .p1, .about .n-title .p2{
        color: #333;
    }
    .about .detail ul{
        list-style: none;
        padding: 0px;
        margin: 0px;
    }
    .about .detail ul li{
        padding-bottom: 12px;
        font-size: 13px;
        padding: 20px;
    }
    .about .detail ul li:last-child{
        padding-bottom: 0px;
    }
    .about.active .n-title, .about.active .detail{
        animation-name: fadeIn;
    }
    @media (min-width: 300px){
        .about .detail ul li{
            padding: 8px 20px;
        }
    }
    @media (min-width: 768px){
        .about .detail, .about .n-title{
            animation: 1s ease  forwards  alternate;
        }
        .about .detail ul li {
            padding-left: 20px;
            padding-right: 20px;
        }
        .about{
            padding-top: 45px;
            padding-bottom: 45px;
        }
    }
    @media (min-width: 992px){
        .about .detail ul li{
            padding: 20px 20px;
        }
    }
    @media (min-width: 1200px){
        .about .detail ul li {
            padding-bottom: 20px;
            font-size: 14px;
            padding-left: 20px;
            padding-right: 20px;
        }
        .about{
            padding-top: 60px;
            padding-bottom: 60px;
        }
        .about .content{
            width: 1100px;
            margin: 0px auto;
        }
    }
    @media (min-width: 1500px){
        .about .content{
            width: 1300px;
            margin: 0px auto;
        }
    }
    @media (min-width: 1800px){
         .about .content{
            width: 1500px;
            margin: 0px auto;
        }
    }
</style>


